

$메인칼라 : #06d194;

.red {
    color: $메인칼라;
}

@mixin my-alert {
    background: #eeeeee;
    padding: 15px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert {
    @include my-alert()
}

.my-alert2 {
    @extend .my-alert;
    background: #ffcccc;
}

.my-alert3 {
    @extend .my-alert;
    background: #f0ffb8;
}

.my-alert p {
    margin-bottom: 0;
}
